.main-aboutus-page {
    background-color: #150F22;
}

.main-gradient {
    background-image: url('../../../../assets/images/internalpages-bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
}

.heading-div {
    padding: 93px 0px;

    h1 {
        color: #FFF;
        font-family: Urbanist;
        font-size: 48px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
    }

    p {
        color: #FFF;
        font-family: Urbanist;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}
.inner-form-div {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 18px;
}
.left-side-form {
    border-radius: 24px;
    background: linear-gradient(130deg, #584772 -1.74%, #2F2A38 108.02%);
    padding: 24px;

    h1 {
        color: #FFF;
        font-family: Urbanist;
        font-size: 40px;
        font-style: normal;
        font-weight: 600;
         text-underline-offset: 24px;
        text-decoration: underline;
    }
    .form-label{
        color: #FFF;
font-family: Urbanist;
font-size: var(--Font-Size-2, 14px);
font-style: normal;
font-weight: 500;
line-height: var(--Line-Heights-10, 20px); /* 142.857% */
letter-spacing: var(--Letter-Spacing-2, 0.1px);
    }
}

.right-other-info {
    padding: 26px 0px 0px 0px;
    h1 {
        color: #FFF;
        font-family: Urbanist;
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 36px;
        margin: 0px;
        text-decoration: underline;
        text-underline-offset: 12px;
    }
}
.email-div{
    .left-icon{
        min-width: 54px;
height: 54px;
        border-radius: 4px;
        display: flex;
        align-items: center;
        justify-content: center;
background: var(--1, linear-gradient(130deg, #492FB2 -1.74%, rgba(73, 47, 178, 0.61) 125.81%));
    }
    .right-email{
        h3{
            color: #FFF;
font-family: Urbanist;
font-size: 20px;
font-style: normal;
font-weight: 600;
line-height: normal;
        }
        h4{
            color: #FFF;
font-family: Urbanist;
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: normal;
        }
    }
}
.map-container {
  border-radius: 15px;
  overflow: hidden;
  display: inline-block;
  background-color: #141021;
  width: 100%;      /* make it responsive */
  height: 200px;    /* control height of map */
  position: relative;
}

.map-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}


/* Default desktop/tablet styles (your current setup works well) */

/* RESPONSIVE MEDIA QUERIES */

/* Tablets (≤ 992px) */
@media (max-width: 992px) {
  .form-div {
    flex-direction: column;
    gap: 40px;
  }

  .left-side-form,
  .right-other-info {
    width: 100% !important;
  }

  .inner-form-div {
    grid-template-columns: 1fr; /* stack inputs */
  }

  .left-side-form h1 {
    font-size: 32px;
  }

  .heading-div h1 {
    font-size: 36px;
  }

  .heading-div p {
    font-size: 16px;
  }
}

/* Mobiles (≤ 576px) */
@media (max-width: 576px) {
  .heading-div {
    padding: 60px 0;
  }

  .heading-div h1 {
    font-size: 28px;
    line-height: 36px;
  }

  .heading-div p {
    font-size: 14px;
    line-height: 20px;
  }

  .form-div {
    gap: 20px;
  }

  .left-side-form {
    padding: 16px;
  }

  .left-side-form h1 {
    font-size: 24px;
    text-align: center;
  }

  .form-label {
    font-size: 13px;
  }

  input,
  textarea {
    font-size: 14px;
  }

  .right-other-info {
    padding: 10px 0 0;
  }

  .right-other-info h1 {
    font-size: 20px;
  }

  .email-div {
    // flex-direction: column;
    gap: 10px;
    text-align: start;
  }

  .map-container {
    height: 180px;
  }
}

.btn-submit {
  background:#523AB1 ;
  border-radius: 8px;
  border: none;
  color: #FFF;
text-align: center;
font-family: Urbanist;
font-size: 14px;
font-style: normal;
font-weight: 600;
line-height: 16px; /* 114.286% */
letter-spacing: var(--Letter-Spacing-3, 0.5px);
padding: 16px;
}